.profile__description {
  margin: 0;
  font-family: 'Inter', Arial, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 90%;
}
@media (max-width: 700px) {
  .profile__description {
    margin: 0 auto;
    font-size: 14px;
    line-height: 17px;
    max-width: 80%;
  }
}
